<template>
  <div class="addForm">
    <div>
      <h3 class="title">详情描述</h3>
      <el-input type="textarea"
                v-model="description"
                placeholder="请输入详情描述"
                @change="onDescription"
                autosize></el-input>
      <span>{{note}}</span>
    </div>
    <h3 class="title">
      文件列表
      <span class="fr addFile" @click="addFiles">
        <i class="el-icon-circle-plus-outline"></i>
      </span>
    </h3>
    <div class="tab-tit clear">
      <TabTit class="fl"
              :item="item"
              :index="index"
              :selected="selected"
              v-for="(item,index) in files"
              @changeTab="onChangeTab(index)"
              :key="item.filename">
      </TabTit>
    </div>
    <div v-for="(item,index) in files"
     :key="index">
      <TabContent
        v-show="selected === index"
        :addHandle="addHandle"
        :files="files"
        :item="item">
      </TabContent>
    </div>
    <div class="btn">
      <el-button type="primary"
                 size="medium"
                 :disabled = "disabled"
                 @click="saveGist">保存</el-button>
    </div>
  </div>
</template>

<script>
  import client from "./../../js/github-client"
  import bus from './../../bus.js'
  import TabContent from '@/components/List/TabContent'
  import TabTit from '@/components/List/TabTit'


  export default {
    name: "AddGist",
    components:{TabContent,TabTit},
    data(){
      return {
        files:[],
        description:"",
        filename:"",
        content:"",
        note:"",
        disabled:false,
        addHandle:false,
        selected:0,
        current:0
      }
    },
    computed:{
      hideDeleteIcon(){
        return this.files && this.files.length  === 1;
      }
    },
    mounted(){
      var filename = "file_"+this.files.length+1;
      this.files.push({
        filename:filename,
        content:this.content
      });
    },
    methods:{
      isDesNull(){
        if(this.description === ""){
          this.note="详情描述不能为空";
          return;
        } else {
          this.note="";
        }
      },
      onDescription(){
        this.isDesNull();
      },
      onChangeTab(index){
        this.selected = index;
      },
      addFiles(){
        this.files.push({
          filename:this.filename,
          content:this.content
        });
        this.addHandle = true;
        this.selected = this.files.length-1;
      },
      deleteFile(index){
        if(index > 0 ){
          this.files.splice(index,1);
        }
      },
      saveGist(){
        this.isDesNull();
        var data = {};
        data.public = true;
        data.description  = this.description;
        data.files={};
        this.files.forEach((item)=>{
          data.files[item.filename] = {
            content:item.content
          }
        })
        console.log(data);
        client.post("/gists",data)
          .then((r)=>{
            this.disabled = true;
            bus.$emit("addListGist",{
              id:r.data.id,
              description: r.data.description
            });
            this.open();
          })
          .catch();
      },
      open(){
        this.$message({
          message: '恭喜你，添加成功',
          type: 'success'
        });
      }
    }
  }
</script>

<style scoped>
  .title{
    background-color: #f5f5f5;
    padding: 7px 12px;
  }
  .tab-tit{
    border-bottom: 2px solid #e4e7ed;
    margin-bottom: 20px;
  }
  a:hover{
    color:#409eff;
  }
  .active{
    border-bottom: 2px solid #409eff;
    position: relative;
    bottom:-2px;
  }
  .addFile{
    color:#409EFF;
  }
  .addFile i{
    cursor: pointer;
    font-size:12px;
    font-weight: bold;
  }
  .el-icon-delete{
    font-size:16px;
    cursor: pointer;
    position: relative;
    top:-10px;
  }
</style>



