<template>
  <div>
    <div>
      <h3 class="title">详情描述</h3>
      <div class="form-item">
        {{description}}
      </div>
    </div>
    <h3 class="title">文件列表</h3>
    <div class="tab-tit clear">
      <TabTit class="fl"
              :item="item"
              :index="index"
              :selected="selected"
              :hideReadGist="hideReadGist"
              v-for="(item,index) in files"
              :key="item.filename"
              @changeTab="onChangeTab(index)">
      </TabTit>
    </div>
    <div v-for="(item,index) in files"
         v-show="selected === index"
      :key="index">
      <div class="form-item">
        <b>{{item.filename}}</b>
      </div>
      <div class="form-item">
        {{item.content}}
      </div>
    </div>
    <el-button size="small" @click="$emit('editGist')" v-show=" !hideReadGist">编辑</el-button>
  </div>
</template>

<script>
  import http from "axios"
  import bus from './../../bus.js'
  import TabTit from '@/components/List/TabTit'

  export default {
    name: "ReadGist",
    components:{TabTit},
    props:["files","description","id","hideReadGist"],
    data(){
      return {
        hideTab:true,
        activeName: "",
        selected: 0
      }
    },
    methods: {
      onChangeTab(index) {
        this.selected = index;
      }
    }
  }
</script>

<style scoped>
  .tab-tit {
    border-bottom: 2px solid #e4e7ed;
    margin-bottom: 20px;
  }
</style>



