<template>
  <li @click="$emit('changeCurrent',index)">
    <router-link :to="{path:'/gists/gist', query:{id:item.id}}">
      <span class="item-desc">{{item.description}} </span>
    </router-link>
    <span class="item-delete fr" @click="deleteCurrentGist(item.id)"><i class="el-icon-delete"></i></span>
  </li>
</template>

<script>
  import bus from "./../../bus.js"
  import http from "axios"

  export default {
    name: "ListItem",
    props:["item","index"],
    mounted(){
        bus.$on("defaultTab",(id) => {
            console.log(id);
        });
    },
    methods:{
      deleteCurrentGist(id){
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          http.delete("https://api.github.com/gists/"+ id,{
            headers: {
              "Authorization": "token " + localStorage.getItem("token")
            }
          })
            .then((r) => {
              console.log(r);
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              bus.$emit("deletePageGist",id);
            })
            .catch();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }

    }
  }
</script>

<style scoped>
  .item-desc{
    width:90%;
    display: inline-block;
  }
  .item-delete {
    color: #f00;
    font-size:12px;
    cursor: pointer;
  }
</style>



