<template>
  <div>
    <h3 class="title">详情描述</h3>
    <el-input type="textarea" v-model="editDes" autosize></el-input>
    <h3 class="title">文件列表</h3>
    <div class="tab-tit clear">
      <TabTit class="fl"
              :item="item"
              :index="index"
              :selected="selected"
              v-for="(item,index) in files"
              :key="item.filename"
              :hideReadGist="hideReadGist"
              @disabledBtn = "onDisableBtn"
              @changeTab="onChangeTab(index)">
      </TabTit>
    </div>
    <TabContent
      :files="files"
      :item="item"
      :index="index"
      :id="id"
      v-for="(item,index) in files"
      :key="index"
      v-show="selected === index"
      @inputChange="onInputChange"
      @contentChange="onContentChange">
    </TabContent>
    <div class="btn">
      <el-button type="primary" size="medium" :disabled="disabledSave"
                 @click="onUpdateItem">保存</el-button>
      <el-button size="medium" @click="addFile">添加</el-button>
    </div>
  </div>
</template>

<script>
  import http from "axios"
  import bus from './../../bus.js'
  import TabTit from '@/components/List/TabTit'
  import TabContent from '@/components/List/TabContent'
  export default {
    name: "Gist",
    props:["description","files","id","hideReadGist"],
    components: {TabTit, TabContent},
    data() {
      return {
        disabledSave:false,
        selected:0,
        editDes:"",
        newContent: "",
        newFileIndex:1,
        data:{}
      }
    },
    watch:{
      'description'(){
        this.editDes = this.description;
      }
    },
    methods: {
      onChangeTab(index) {
        this.selected = index;
      },
      onInputChange(index){
        this.$set(this.files[index],"nameChanged",true);
        this.disabledSave = false;
        console.log(index);
      },
      onContentChange(){
        this.disabledSave = false;
      },
      onDisableBtn(){
        this.disabledSave = false;
      },
      addFile(){
        var filename = "file_"+this.newFileIndex++;
        this.files.push({
          filename: filename,
          content: this.newContent
        });
        this.disabledSave = false;
      },
      onUpdateItem() {
        this.data.description = this.editDes;
        if(this.editDes.length===0){
          alert("详情描述不能为空");
          return;
        }
        this.data.files = {};
        var _self = this;
        this.files.forEach(function (item) {
            if(item.oldname !== undefined){
//                更新文件
                if(item.nameChanged !== undefined){
                    _self.data.files[item.oldname] = {
                      filename:item.filename,
                      content:item.content
                    }
                }
//                删除文件
                if(item.deleted !== undefined){
                    _self.data.files[item.oldname] = null;
                }
            } else {
              if(item.nameChanged !== undefined){
//                    添加文件
                  _self.data.files[item.filename] = {
                    content:item.content
                  }
                }
            }
        })
        console.log(this.data);
        http.patch("https://api.github.com/gists/" + this.id, this.data, {
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        }).then((r) => {
          console.log(r);
          // this.files = Object.values(r.data.files);
          // this.files.forEach(function (val) {
          //   val["oldname"] = val.filename;
          // })
          this.open();
          bus.$emit("updateList",{
            id:this.id,
            files:r.data.files,
            description:this.editDes
          });
        })
          .catch();
        this.disabledSave = true;
        this.gistChanged = false;
      },
      open(){
        this.$message({
          message: '恭喜你，保存成功',
          type: 'success'
        });
      }
    }
  }
</script>

<style scoped>
  .tab-tit {
    border-bottom: 2px solid #e4e7ed;
    margin-bottom: 20px;
  }
  .title{
    background-color: #f5f5f5;
    padding: 7px 12px;
  }
</style>



