<template>
  <div>
    <Load :hideLoad="hideLoad"></Load>
    <EditGist
      :class="{hide:!hideReadGist}"
      :hideReadGist="hideReadGist"
      :description="description"
      :id="id"
      :files="files">
    </EditGist>
    <ReadGist
      :hideReadGist="hideReadGist"
      :description="description"
      :files="files"
      :id="id"
      @editGist="onEditGist"
      :class="{hide:hideReadGist || !hideLoad}">
    </ReadGist>
  </div>
</template>

<script>
  import http from "axios"
  import bus from './../../bus.js'
  import EditGist from '@/components/List/EditGist'
  import ReadGist from '@/components/List/ReadGist'
  import Load from '@/components/List/Load'

  export default {
    name: "Gist",
    components:{EditGist,ReadGist,Load},
    data(){
      return {
        id: this.$route.query.id,
        hideReadGist:false,
        hideTab:true,
        gistChanged: false,
        disabledSave: false,
        hideLoad: false,
        files:{},
        activeName: "",
        selected: 0,
        description: ""
      }
    },
    beforeRouteUpdate(to, from, next) {
      console.log(to);
      if (this.gistChanged) {
        next(false);
        window.confirm("请编辑完成");
      } else {
        this.hideLoad = false;
        this.getFiles(to.query.id);
        this.id = to.query.id;
        next();
      }
    },
    mounted() {
      this.getFiles(this.id);
      bus.$on("goReadGist",() => {
        this.hideReadGist = false;
      });
    },
    methods:{
      getFiles(id) {
        http.get("https://api.github.com/gists/" + id, {
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        }).then((r) => {
          this.hideLoad = true;
          this.description = r.data.description;
          this.files = Object.values(r.data.files);
          this.files.forEach(function (val) {
            val["oldname"] = val.filename;
          });
          this.activeName = this.files[0].filename;
        })
          .catch();
      },
      onEditGist(){
        this.hideReadGist = true;
      }
    }
  }
</script>

<style scoped>

</style>



